import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { styled } from '@mui/material/styles';
import IconButton from '@mui/material/IconButton';
import PhoneIcon from '@mui/icons-material/Phone';
import MailIcon from '@mui/icons-material/Mail';

// Styled Box for the hero section with a dynamic background
const HeroBox = styled(Box)(({ theme, backgroundImage }) => ({
  color: 'white',
  backgroundColor: '#1a1a1a',
  backgroundImage: `url(${backgroundImage})`,
  //backgroundSize: 'cover',
  backgroundSize: 'cover', // Ensures the image covers the box completely
 // backgroundPosition: 'center',
  backgroundRepeat: 'no-repeat', // Prevents tiling
  minHeight: '600px', 
  backgroundPosition: 'center',
  padding: theme.spacing(12),
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  textAlign: 'left',
  position: 'relative',
  overflow: 'hidden',
  zIndex: 1,
  [theme.breakpoints.down('md')]: {
    padding: theme.spacing(6),
    textAlign: 'center',
  },
}));

export default function Hero() {
  const [currentTextIndex, setCurrentTextIndex] = React.useState(0);

  const content = [
    {
      text: "We import the finest Makhana, Rice, Walnuts, and Corns from around the world.",
      backgroundImage: "https://i.pinimg.com/originals/33/97/45/33974528aafacff2a8180c581068c877.jpg",
    },
    {
      text: "Delivering premium quality Rice and Walnuts to our customers globally.",
      backgroundImage: "https://static.vecteezy.com/system/resources/previews/030/354/034/non_2x/white-rice-in-sack-ai-generated-photo.jpg",
    },
    {
      text: "Your trusted partner in importing Corns and other premium commodities.",
      backgroundImage: "https://5.imimg.com/data5/SELLER/Default/2024/8/440298534/ED/RR/SZ/126638229/premium-california-walnut.png",
    },
  ];

  React.useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTextIndex((prev) => (prev + 1) % content.length);
    }, 4000); // Change text and background every 4 seconds

    return () => clearInterval(intervalId);
  }, [content.length]);

  return (
    <>
      <style>{`
        .floating-text {
          animation: float 4s ease-in-out infinite;
        }

        @keyframes float {
          0% {
            transform: translateY(0);
          }
          50% {
            transform: translateY(-10px);
          }
          100% {
            transform: translateY(0);
          }
        }
      `}</style>

      <HeroBox backgroundImage={content[currentTextIndex].backgroundImage}>
        <Box sx={{ maxWidth: 800 }}>
          <Typography
            variant="h2"
            gutterBottom
            sx={{ lineHeight: 1.2 }}
            className="floating-text"
          >
            {content[currentTextIndex].text}
          </Typography>
          <Typography variant="body1" paragraph sx={{ fontSize: '1.2rem' }}>
            Your gateway to premium imported commodities. Experience quality and trust with us.
          </Typography>
          <Grid container spacing={4}>
            <Grid item xs={12} md={4}>
              <Typography variant="h5" fontWeight="bold">500+</Typography>
              <Typography variant="body2" color="grey.400">Products Imported</Typography>
            </Grid>
            <Grid item xs={12} md={4}>
              <Typography variant="h5" fontWeight="bold">50K+</Typography>
              <Typography variant="body2" color="grey.400">Happy Clients</Typography>
            </Grid>
            <Grid item xs={12} md={4}>
              <Typography variant="h5" fontWeight="bold">4.9</Typography>
              <Typography variant="body2" color="grey.400">Customer Rating</Typography>
            </Grid>
          </Grid>
        </Box>
      </HeroBox>
    </>
  );
}
